
import { Link } from 'react-router-dom'
import MetaData from '../layouts/MetaData'
import CheckoutSteps from './CheckoutSteps'
import { Row,Col } from 'react-bootstrap';
import  {useEffect,useState} from 'react'
import { useHistory } from "react-router-dom";
import Loader from '../layouts/Loader'
import { createOrder } from '../../actions/cartAction';
import { useAlert } from 'react-alert';
function AttentionCommande({loading,user,setCartLen}) {
    const [res, setRes] = useState(null);
    const [erreur, setErreur] = useState(null);
    const [loading2, setLoding2] = useState(false);
    const alert = useAlert();

    let history = useHistory();
    useEffect(() => {
        if(user)
        history.push('/panier')
        !sessionStorage.getItem('comandeInfo')&&history.push('/confimerCommande')
    }, [user])
    useEffect(() => {
        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
    }, [erreur])
    useEffect(() => {
        if (res&&res.success) {
            localStorage.removeItem('cartItems')
            setCartLen(0)
            history.push('/succes') 
        }
    }, [res])
    const passerCmd = () => {
            const commande = JSON.parse(sessionStorage.getItem('comandeInfo'));
            setLoding2(true)
            createOrder(commande,setRes,setErreur,setLoding2)       
    }

    return (
        <>
        {!loading ? <Loader /> : 
            <>
                <MetaData title={'login avant la passation'} />
                <CheckoutSteps livraison confirmOrder attention/>
                <div className="row wrapper">
                    <div className="col-10 col-lg-5">
                    <div className="shadow-lg" >
                        <h2 className="mb-4" align='center'>Merci de bien lire ça avant de passer votre commande</h2>
                        <hr style={{margin:"3% 4%"}}/>
                        <div style={{fontSize:"1.15rem",margin:"0% 4%",fontWeight:500}}>
                        Merci de vous connecter avant de valider votre commande pour plusieurs raisons, notamment pour sauvegarder vos données et vous permettre de suivre les etats de vos commandes avec plus de sécurité                </div>
                        <hr style={{margin:"3% 4%"}}/>

                        <div style={{fontSize:"1.15rem",margin:"0% 4%",fontWeight:500}}>
                        الرجاء تسجيل الدخول قبل المصادقة على قائمة المشتريات لعدة أسباب منها حفظ بياناتكم و تمكينكم من متابعة متابعة مراحل طلباتكم بمزيد من الامان 
                        </div>
                        <hr />
                        <Row>
                            <Col align='center' sm="12">
                                <Link
                                to='/login?url=/confimerCommande'
                                style={{margin:"2% 0%"}}
                                className="btn  py-2 review-btn"
                                >
                                            Connecter
                                </Link>
                            
                            </Col>
                            <Col align='center'>
                                <button
                                style={{margin:"4% 0%"}}
                                className="btn  py-2 review-btn"
                                onClick={passerCmd}
                                disabled={loading2}
                                >
                                                Passer sans connecter
                                </button>
                            </Col>
                        
                        </Row>
                    </div>                    
                    </div>


                </div>
            </>}
        </>    
    )
}

export default AttentionCommande
